<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import store from '@/vuex'

export default {
  name: 'MyTokensPage',
  setup() {
    const router = useRouter()
    
    const activeTab = ref('created')
    const userTokens = ref(null)
    const loading = ref(true)
    const error = ref('')
    const refreshInterval = ref(null)
    const lastUpdated = ref(null)

    const isAuthenticated = computed(() => !!store.getters.userTag)
    const userTag = computed(() => store.getters.userTag)

    const createdLaunchedCount = computed(() => {
      if (!userTokens.value) return 0
      return userTokens.value.created?.filter(token => 
        token.status === 'launched' || token.status === 'locked'
      ).length || 0
    })

    const squadLaunchedCount = computed(() => {
      if (!userTokens.value) return 0
      return userTokens.value.squad?.filter(token => 
        token.status === 'launched' || token.status === 'locked'
      ).length || 0
    })

    const loadUserTokens = async (showLoading = true) => {
      if (!isAuthenticated.value) {
        loading.value = false
        return
      }

      if (showLoading) {
        loading.value = true
      }
      error.value = ''

      try {
        const response = await axios.get('/my/tokens')
        
        if (response.data.success) {
          userTokens.value = response.data.data
          lastUpdated.value = new Date()
        } else {
          throw new Error(response.data.message || 'Failed to load user tokens')
        }
      } catch (err) {
        console.error('Error loading user tokens:', err)
        if (showLoading) {
          if (err.response?.status === 401) {
            error.value = 'Please login to view your tokens'
          } else {
            error.value = err.response?.data?.message || err.message || 'Error loading tokens'
          }
        }
      } finally {
        if (showLoading) {
          loading.value = false
        }
      }
    }

    const navigateToToken = (tokenId) => {
      router.push(`/token/${tokenId}`)
    }

    const getStatusText = (status) => {
      const statusMap = {
        active: 'Collecting Squad',
        minting: 'Preparing Launch',
        launched: 'Launched',
        locked: 'Liquidity Locked'
      }
      return statusMap[status] || 'Unknown'
    }

    const formatLamports = (lamports) => {
      if (!lamports) return '0'
      return (lamports / 1e9).toFixed(2)
    }

    const formatTokens = (tokens) => {
      if (!tokens) return '0'
      return (tokens / 1000000).toLocaleString()
    }

    const formatDate = (dateString) => {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toLocaleDateString('en-US', { 
        month: 'short', 
        day: 'numeric'
      })
    }

    const formatTime = (date) => {
      return new Intl.DateTimeFormat('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      }).format(date)
    }

    const startAutoRefresh = () => {
      if (!isAuthenticated.value) return
      
      refreshInterval.value = setInterval(() => {
        loadUserTokens(false)
      }, 30000) // Каждые 30 секунд
    }

    const stopAutoRefresh = () => {
      if (refreshInterval.value) {
        clearInterval(refreshInterval.value)
        refreshInterval.value = null
      }
    }

    onMounted(async () => {
      await loadUserTokens()
      startAutoRefresh()
    })

    onUnmounted(() => {
      stopAutoRefresh()
    })

    return {
      activeTab,
      userTokens,
      loading,
      error,
      lastUpdated,
      isAuthenticated,
      userTag,
      createdLaunchedCount,
      squadLaunchedCount,
      loadUserTokens,
      navigateToToken,
      getStatusText,
      formatLamports,
      formatTokens,
      formatDate,
      formatTime
    }
  }
}
</script>

